
import { defineComponent } from 'vue';
import UiInput from '@/components/UiInput.vue';
import UiButton from '@/components/UiButton.vue';
import userManager from '@/utills/UserManager';

export default defineComponent({
  name: 'SignUp',
  components: {
    UiButton,
    UiInput,
  },
  props: {
    code: String,
  },
  data: () => ({
    validationArray: new Array<any>(),
    validating: false,
    password: '',
    newPassword: '',
  }),
  computed: {
    showCurrentPassword() {
      console.log(this.code);
      return this.code === undefined;
    },
  },
  methods: {
    async wait() {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(true);
        }, 250);
      });
    },
    async validate() {
      this.validating = true;
      this.$formBus.on('input:save', this.handleSaveEvent.bind(this));
      this.$formBus.emit('save');
      this.wait();
      this.$formBus.off('input:save', this.handleSaveEvent.bind(this));
      try {
        await Promise.all(this.validationArray);
      } catch (e) {
        console.error(e);
        return false;
      }
      return true;
    },
    handleSaveEvent(pending: any) {
      this.validationArray.push(pending);
    },
    async sendForm() {
      const validation = await this.validate();
      if (validation) {
        const result = await userManager.changePassword({
          newPassword: this.newPassword,
          password: this.password,
        });
        if (result.result) {
          this.$router.push({
            name: 'Profile',
          });
        }
      }
    },
  },
  mounted() {
    console.log(this.code);
  },
});
